.colorway{width:100%;position:relative}.preview,.previewActive{display:flex;cursor:pointer;padding:.75em 0;position:relative;align-items:center;background:rgba(0,0,0,0);width:100%;justify-content:space-between;color:var(--light-3)}.preview:before,.previewActive:before{content:"";opacity:0;margin-left:-1em;width:calc(100% + (1em)*2);height:100%;display:block;position:absolute;transition:opacity .2s;background:var(--accent-transparent)}.preview:focus,.previewActive:focus{text-decoration:underline}.preview:focus:before,.previewActive:focus:before,.preview:hover:before,.previewActive:hover:before{opacity:1}.previewActive{cursor:initial}.previewActive:after{position:absolute;display:block;background:var(--accent);top:50%;transform:translate(-1em, -50%);width:3px;content:"";height:50%}.info{text-align:left;z-index:1}.title{text-align:left;font-size:16px;z-index:1;width:100%;margin:0}.swatches{z-index:1;display:flex;position:relative;justify-content:space-between;border-radius:5px;overflow:hidden}.swatches:after{content:"";border-radius:5px;width:100%;height:100%;top:0;left:0;position:absolute;box-shadow:0 2px 2px rgba(0,0,0,.15),inset 0 2px 2px rgba(255,255,255,.15);z-index:1}.swatch{width:40px;height:40px;display:flex;align-items:center;justify-content:center}.swatch span{font-family:"legends";font-size:34px;padding-top:19px;padding-left:17px}.description{padding:0px 0;font-size:12px;margin:0 .5em 0 0;display:inline-block;color:var(--light-1);text-transform:uppercase}.actions{display:flex;font-size:14px;color:var(--light-1)}.action{display:flex;cursor:pointer;margin:6px 0 0 0;color:var(--accent);text-decoration:underline}.action:after{display:none;opacity:0;content:"âœ•";margin-left:5px}.action svg{margin-right:4px;width:15px}.action:focus,.action:hover{color:#fff}.action:focus:after,.action:hover:after{display:inline;opacity:1}